import * as React from 'react';
import { COLORS } from '@src/configs';

import { Col, HomeContainer, Row } from './home.styled';
import { useAppContext } from '../../context/app.context';

type IHomeProps = {};
const Home = (_: IHomeProps) => {
  const [state] = useAppContext();
  const [selected, setSelected] = React.useState({});
  const onTicketSelect = (i, j) => {
    if (selected[`${i}_${j}`]) {
      setSelected({ ...selected, [`${i}_${j}`]: 0 });
    } else {
      setSelected({ ...selected, [`${i}_${j}`]: 1 });
    }
  };
  return (
    <HomeContainer>
      {state.tickets.map((item: any, rowIndex: number) => {
        return (
          <Row>
            {item.map((_item: any, colIndex: number) => {
              return (
                <Col
                  onClick={() => onTicketSelect(rowIndex, colIndex)}
                  color={COLORS[_item.type]}
                  selected={!!selected[`${rowIndex}_${colIndex}`]}
                />
              );
            })}
          </Row>
        );
      })}
    </HomeContainer>
  );
};
export default Home;
